<template>
  <div class="specialty-store-page" id="target" ref="toTop" @scroll="handleScroll">
    <skeleton v-if="skeletonLoading" :nums="3" fheight="4rem"></skeleton>
    <div v-else>
        <lazy-search hiddenId='pop-up-hidden' defaultRoute @go-back="searchComponentShow=false" @search='searchEvent' v-if='searchComponentLoad' v-show='searchComponentShow' :show='searchComponentShow'/>
        <div id="topAnchor"></div>
        <!-- 头部 -->
        <Header v-if="!$route.meta.hideHeader" :leftArrow="!$route.query.browerFirstIn" :leftClick="goBack" class="specialty-store-header">
          <span slot="right" @click="share">
            <img v-show="showShareFlag" class="header-img" :src="$globalIcon.blackShareIcon">
          </span>
        </Header>

        <!-- 搜索 -->
        <div class="search-area">
          <div class="search" @click="fnOpenSearch">
            <img src="data:image/png;base64,">
            <span class="text">{{searchWord}}</span>
          </div>
        </div>
        <!-- 主体内容 -->
        <div class="main" id='pop-up-hidden'>
          <template v-if="bannerList && bannerList.length>0">
          <!-- 轮播图 -->
          <van-swipe  ref='vanSwiper' :autoplay="100000" indicator-color="white" class="banner">
            <van-swipe-item v-for="(item, index) in bannerList" :key="index" class="banner-items">
              <van-image
                height="2.8rem"
                width="100%"
                :src="item.pic | imageToHttps | imageThumb('500') | imageToWebp($webpSurpport)"
                @click="bannerClick(item)"/>
            </van-swipe-item>
          </van-swipe>
          </template>
          <!-- 场馆 -->
          <div class="province-store" :class="regionList.length >4 ? 'orbit-wrapper' : 'box-wrapper'">
            <div v-for="(item, index) in regionList" :key="item.titlg" class="p-items" @click="goSpecialDetils(item)">
              <div class="p-img" :style="{background: bgColor[index % 4]}">{{item.simpleName}}</div>
              <span class="p-title">{{item.regionName}}</span>
            </div>
          </div>
          <!-- 限时抢购 -->
          <!-- v-if="flash && flash.flashProductList && flash.flashProductList.length" :flash="flash" -->
          <flash-Sale v-if="showFlash.products && showFlash.products.length" :activeStatus="activeStatus" :flash="showFlash" />
          <!-- 新品预约 -->
          <lazy-component v-if="reverse && reverse.acReserveList && reverse.acReserveList.length">
            <div class="fa-title">
              <p class="fa-text">新品预约</p>
              <div class="more" @click="goAppointment">
                <span class="more-text">更多</span>
                <van-icon name="arrow" color="#bbb"/>
              </div>
            </div>
            <new-order :reverse="reverseTwo" />
          </lazy-component>
          <!-- 特产饮品 -->
          <lazy-component v-if="drinkList.bannerList && drinkList.topBanner">
            <special-drink :drinkList="drinkList" />
          </lazy-component>
          <!-- 特产吃食 -->
          <lazy-component v-if="foodList.productList || foodList.topBanner">
            <special-snacks :foodList="foodList" />
          </lazy-component>
          <!-- 商品列表 -->
          <lazy-component>
          <div ref="bl">
            <bottom-list :typeList="typeList" :tabFixed="tabFixed" />
          </div>
          </lazy-component>
        </div>
        <!-- 购物车 -->
        <!-- <div class="cart" :data-num="cartNumber > 99 ? ' 99+' : cartNumber" :class="cartNumber ? '' : 'no-tag'" @click="goCart">
          <img class="cart-img" src="data:image/png;base64," >
        </div> -->
        <!-- 返回箭头 -->
        <!-- <div v-show="showArrow" class="to-head" @click="toHead" :style="{backgroundImage: `url(${$globalIcon.backToTopIcon})`}"></div> -->
        <!-- 返回顶部 -->
        <top-feedBack :isShow="showToTopFlag" @hiddenFeedBack="hiddenFeedBack" @toTop="fnScrollToTop"></top-feedBack>
    </div>
  </div>
</template>
<script>
import skeleton from '@/components/skeleton'
import Header from '@/components/header'
// import topFeedBack from '@/components/top-feedBack/index'
import flashSale from './components/flash-sale.vue' // 限时抢购
// import newOrder from './components/new-order.vue' // 新品预约
// import specialDrink from './components/special-drink.vue' // 特产饮品
// import specialSnacks from './components/special-snacks.vue' // 特产吃食
// import bottomList from './components/bottom-list.vue'// 商品列表
// import utils from '@/utils/utils'
import { getSpecialGoodsMain, getActiveInfo, getGoodsType } from '@/http/service/specialtyStore'
import { mapState, mapActions } from 'vuex'
import mixins from '@/components/top-feedBack/mixins_inpage_scroll.js'
import commons from '@/utils/commons.js'

const lazySearch = () => import(/* webpackChunkName: "searchComponent" */'@/components/search/index.vue')

const newOrder = () => import(/* webpackChunkName: "zhixuan" */'./components/new-order.vue');
const specialDrink = () => import(/* webpackChunkName: "zhixuan" */'./components/special-drink.vue')
const specialSnacks = () => import(/* webpackChunkName: "zhixuan" */'./components/special-snacks.vue')
const bottomList = () => import(/* webpackChunkName: "zhixuan" */'./components/bottom-list.vue')

export default {
  mixins: [mixins],
  components: {
    skeleton,
    Header,
    flashSale,
    newOrder,
    specialDrink,
    specialSnacks,
    bottomList,
    lazySearch,
    // topFeedBack
  },
  activated() {
    let scrollTop = this.$route.meta.scrollTop;
    if (scrollTop && scrollTop !== 0) {
      // 初次进入页面不执行，返回页面执行
      let $content = document.querySelector('#target');
      if ($content) {
        this.$nextTick(() => {
          $content.scrollTop = scrollTop;
        })
      }
    }
  },
  data() {
    return {
      searchComponentLoad: false,
      searchComponentShow: false,

      skeletonLoading: true,

      showShareFlag: false, // 是否显示分享按钮
      searchWord: '搜索更多特产',
      showArrow: false, // 返回箭头
      baseInfo: {}, // 基本信息
      bannerList: [], // 顶部轮播
      foodList: {}, // 特产吃食
      regionList: [], // 场馆列表
      drinkList: {}, // 饮品
      flash: {}, // 限时抢购
      showFlash: {}, // 需要展示的限时抢购活动
      reverse: {}, // 新品预约
      bgColor: [ // 主页场馆背景色
        'linear-gradient(-45deg, #FF9700, #FFD161 )',
        'linear-gradient(-45deg, #FF7998, #ED2856 )',
        'linear-gradient(-45deg, #00E5AD, #03B77B )',
        'linear-gradient(-45deg, #42C8FF, #1496FF )',
      ],
      typeList: [], // 底部商品分类
      // areaList: [], // 全国场馆
      activeStatus: 1, // 限时抢购当前展示场次的状态  1：进行中 0：预热中
      tabFixed: false,
      scrollTarget: '#target', // 页面滚动元素

    }
  },
  computed: {
    location() {
      return this.$store.getters['getlocationInfo']
    },
    ...mapState('cart', ['cartNumber']),
    userId() {
      return this.$store.getters['mId']
    },
    reverseTwo() { // 主页预约抢购数据展示 至多取两条
      if (Array.isArray(this.reverse.acReserveList)) {
        return this.reverse.acReserveList.length > 2 ? this.reverse.acReserveList.slice(0, 2) : this.reverse.acReserveList
      } else {
        return []
      }
    }
  },
  watch: {
    /**
     * @description 获取活动进行中最近的场次；若没有，获取即将开始最近的场次；还没有，返回空
     */
    flash() {
      let now = [] // 进行中的场次
      let feature = [] // 即将开始的场次
      if (Array.isArray(this.flash.list)) {
        this.flash.list.map(item => {
          if (item.startTime < item.systemTime) {
            now.push(item)
          } else {
            feature.push(item)
          }
        })
        // 过滤场次中开始时间最靠前的
        if (now.length) {
          this.activeStatus = 1
          this.showFlash = now.reduce((prev, next) => {
            return prev.startTime < next.startTime ? prev : next
          })
        } else if (feature.length) {
          this.activeStatus = 0
          this.feature.sort((a, b) => { // 根据开始时间排序
            return a.startTime > b.startTime ? 1 : -1
          })
          this.feature.map(item => {
            this.showFlash = item.products[0].preheatingTime <= item.systemTime ? item : {} // 判断是否到预热时间
          })
        } else {
          this.showFlash = {}
        }
      } else {
        this.showFlash = {}
      }
    }
  },
  created() {
    this.initData()
  },
  // beforeRouteEnter(to, from, next) {
  //   // 判断首页进入智选，刷新数据
  //   next(vm => {
  //     if (from.fullPath === '/') {
  //       vm.initData()
  //     }
  //   })
  // },
  beforeRouteLeave(to, from, next) {
    this.tabFixed = false
    if (from.meta.keepAlive) {
      // 只针对 keep-alive 的页面记录滚动位置
      let $content = document.querySelector('#target'); // 列表的外层容器
      let scrollTop = $content ? $content.scrollTop : 0;
      console.log('scrollTop', scrollTop)
      from.meta.scrollTop = scrollTop;
    }
    next()
  },
  mounted() {
    // window.addEventListener('scroll', utils.throttle(this.handleScroll))
    document.documentElement.scrollTop = 0
    commons.swipeResize(this, 'vanSwiper')
  },
  methods: {
    ...mapActions('cart', ['getCartNumber']),
    /**
     * @description 初始化数据
     */
    async initData() {
      this.getSpecialGoodsMain()
      this.getActiveInfo()
      this.getGoodsType()
      // this.getCharaRegions()
      if (this.$util.isHaierApp() || this.$util.isSgRn()) {
        // U+ 显示分享按钮
        this.showShareFlag = true
        // U+ 登陆处理
        const authFlag = await this.$store.dispatch('handleUpAuth')
        if (authFlag) {
          this.getCartNumber()
        }
      } else {
        if (this.$store.getters.getLoginStatus) {
          this.getCartNumber()
        }
      }
    },
    /**
     * @desctiption 基本元素信息
     */
    getSpecialGoodsMain() {
      return new Promise((resolve, reject) => {
        getSpecialGoodsMain({
          streetId: this.location.townshipId
        }).then(res => {
          this.skeletonLoading = false
          if (res.success) {
            this.bannerList = res.data.topBannerList.bannerList || [] // 顶部轮播
            this.foodList = res.data.foodList || {} // 特产吃食
            this.regionList = res.data.regionList.regionList || []
            this.drinkList = res.data.drinkList // 饮品
          } else {
            this.$toast(res.message)
          }
          // resolve(res)
        }).catch(err => {
          console.log(err)
        })
      })
    },
    /**
     * @description 活动信息接口
     */
    getActiveInfo() {
      console.log(this.location)
      getActiveInfo({
        cityId: this.location.cityId,
        districtId: this.location.districtId,
        from: 2,
        provinceId: this.location.provinceId,
        streetId: this.location.townshipId
      }).then(res => {
        if (res.success) {
          this.flash = res.data.flash // 限时抢购
          this.reverse = res.data.reverse // 新品预约
        } else {
          this.$toast(res.message)
        }
      }).catch(err => {
        console.log(err)
      })
    },
    /**
     * @description 底部商品分类
     */
    getGoodsType() {
      getGoodsType({
        type: 3
      }).then(res => {
        if (res.success) {
          this.typeList = res.data || []
        } else {
          this.$toast(res.message)
        }
      }).catch(err => {
        console.log(err)
      })
    },
    /**
     * @description 更多场馆
     */
    // getCharaRegions() {
    //   getCharaRegions({}).then(res => {
    //     if (res.success) {
    //       this.areaList = res.data || []
    //     } else {
    //       this.$toast(res.message)
    //     }
    //   })
    // },
    /**
     * @description 分享
     */
    share() {
      this.$util.gioTrackWithVariable('mall_specialty_click_share')
      let url = location.href
      // 分享方法
      this.$util.inAppShare({
        url: url.includes('?') ? `${url}&shareId=${this.userId}` : `${url}?shareId=${this.userId}`
      })
    },
    /**
     * @description 搜索
     */
    fnOpenSearch() {
      if (!this.searchComponentLoad) {
        lazySearch().then(() => {
          this.searchComponentLoad = true;
          this.searchComponentShow = true;
        })
        return;
      }
      this.searchComponentShow = true;
    },
    /**
     * @description 跳转搜索页面
     */
    searchEvent(keyword) {
      this.searchComponentShow = false;
    },
    /**
     * @ description 点击轮播图跳转
     */
    bannerClick(item) {
      const { linkType, link, relationId } = item
      this.$store.dispatch('bannerClick', { hyperLinkType: String(linkType), hyperLink: link, relationId })
    },
    hiddenFeedBack() {
      this.showToTopFlag = false
    },
    /**
     * @description 新品预约 更多
     */
    goAppointment() {
      this.$router.push({
        path: '/orderMore',
        query: {
          list: JSON.stringify(this.reverse.acReserveList || [])
        }
      })
    },
    /**
     * @description 特产馆
     */
    goSpecialDetils(item) {
      // console.log(item, '---item -----')
      this.$router.push({
        name: 'smartChoiceProvince',
        query: {
          regionId: item.regionId
        }
      })
    },
    // /**
    //  * @description 监听滚动
    //  */
    // handleScroll() {
    //   let _scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop
    //   this.showArrow = _scrollTop > 300
    // },
    handleScroll() {
      let _blTop = this.$refs.bl.getBoundingClientRect().top
      this.tabFixed = _blTop < 46
      let _scrollTop = this.$refs.toTop.scrollTop
      this.showToTopFlag = _scrollTop > 300
    },
    /**
     * @description 返回到品类选择位置
     */
    toHead() {
      document.querySelector('#topAnchor').scrollIntoView({
        block: 'start',
        behavior: 'smooth', // 平滑过渡
      })
      this.showArrow = false
    },
    /**
     * @description 进入购物车
     */
    goCart() {
      this.$router.push('/cart')
    },
    goBack() {
      this.$keepAliveDestroy()
      this.$store.dispatch('doHistoryBack');
    },
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>
