<template>
  <div class="flash-sale-parts">
    <!-- 标题 -->
    <div class="fa-title">
      <p class="fa-text">限时抢购</p>
      <div class="more" @click="goMore">
        <span class="more-text">更多</span>
        <van-icon name="arrow" color="#bbb"/>
      </div>
    </div>
    <!-- 倒计时 -->
    <van-count-down :time="downTime" class="count-down">
      <template v-slot="timeData">
        <span class="count-down-name">{{flash.timeStr}}场</span>
        <span class="text">{{activeStatus ? '距结束' : '距开始'}}</span>
        <span class="block">{{ timeData.hours }}</span>
        <span class="colon">:</span>
        <span class="block">{{ timeData.minutes }}</span>
        <span class="colon">:</span>
        <span class="block">{{ timeData.seconds }}</span>
      </template>
    </van-count-down>
    <!-- 商品列表 -->
    <div class="fa-goods-list">
      <div v-for="item in flash.products || []" :key="item.productId" class="list-items" @click="goDetail(item)">
        <van-image
          height="2rem"
          width="2rem"
          fit="cover"
          lazy-load
          :src="item.imageUrl | imageToHttps | imageThumb('200') | imageToWebp($webpSurpport)"
        />
        <div class="goods-name">{{item.productName}}</div>
        <div class="prod-price">
          <span class="symbol">￥</span>{{item.flashsalePrice | formatPrice}}
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    flash: {
      type: Object,
      default: () => { return {} }
    },
    activeStatus: { // 当前场次状态   1：进行中  0：预热中
      type: Number,
      default: 1
    }
  },
  data() {
    return {}
  },
  computed: {
    downTime() {
      return this.activeStatus ? (this.flash.endTime - this.flash.systemTime) : (this.flash.startTime - this.flash.systemTime)
    }
  },
  methods: {
    /**
     * @description 更多
     */
    goMore() {
      // this.$router.push('/flashSale')
      this.$router.push({
        path: '/flashMore',
        query: {}
      })
    },
    /**
     * @description 进入详情
     */
    goDetail(item) {
      this.$router.push(`/goodsDetail?productId=${item.productId}`)
      // if (this.$util.isSgRn()) {
      //   window.postMessage(
      //     JSON.stringify({
      //       type: 'goProduct',
      //       data: {
      //         id: item.productId
      //       }
      //     })
      //   )
      // } else {
      //   this.$router.push(`/goodsDetail?productId=${item.productId}`)
      // }
    }
  }
}
</script>
