let _resizeThrottler;

export default {
  /**
   * 解决窗口大小发生变化时，vant swipe尺寸不更新问题，建议所有使用vant swipe组件的页面添加该方法
   * @param {*} vue Vue实例
   * @param {*} swiper  swipe对应的ref name
   */
  swipeResize(vue, swiper) {
    const _swiper = swiper;
    const _vue = vue;
    (function() {
      var resizeTimeout;
      _resizeThrottler = function resizeThrottler() {
        // ignore resize events as long as an actualResizeHandler execution is in the queue
        if (!resizeTimeout) {
          resizeTimeout = setTimeout(function() {
            resizeTimeout = null;
            actualResizeHandler();

            // The actualResizeHandler will execute at a rate of 15fps
          }, 1000);
        }
      }
      window.addEventListener('resize', _resizeThrottler, false);

      function actualResizeHandler() {
        // handle the resize event
        if (_vue && _vue.$refs && _vue.$refs[_swiper]) {
          _vue.$refs[_swiper].resize()
        }
      }
    }());
  },
  removeSwipeResize() {
    window.removeEventListener('resize', _resizeThrottler, false);
  }
}
